import React from 'react';
import bridge from '@vkontakte/vk-bridge';
import {View, Panel, Group, PanelHeader, Spinner, Header, Card, CardGrid, Avatar, Snackbar, ConfigProvider, PanelHeaderButton, ModalPage, ModalPageHeader, ModalRoot, List, Button, Cell, FormLayout, Input, Tabs, TabsItem} from '@vkontakte/vkui';
import Axios from 'axios';

import '@vkontakte/vkui/dist/vkui.css';

import Modal from './panels/Modal';
import Icon48Camera from '@vkontakte/icons/dist/48/camera';
import Icon56LinkCircleOutline from '@vkontakte/icons/dist/56/link_circle_outline';
import { VKMiniAppAPI } from '@vkontakte/vk-mini-apps-api';
import Icon28CancelCircleOutline from '@vkontakte/icons/dist/28/cancel_circle_outline';
import Icon28DoneOutline from '@vkontakte/icons/dist/28/done_outline';
import Icon28Play from '@vkontakte/icons/dist/28/play';
import Icon28Pause from '@vkontakte/icons/dist/28/pause';
import Icon56UserAddOutline from '@vkontakte/icons/dist/56/user_add_outline';
import Icon56DeleteOutlineAndroid from '@vkontakte/icons/dist/56/delete_outline_android';
import Icon56TouchIdOutline from '@vkontakte/icons/dist/56/touch_id_outline';
import Icon56MoneyTransferOutline from '@vkontakte/icons/dist/56/money_transfer_outline';

import Icon56FaceIdOutline from '@vkontakte/icons/dist/56/face_id_outline';
import Icon56WriteOutline from '@vkontakte/icons/dist/56/write_outline';
import Icon16Fire from '@vkontakte/icons/dist/16/fire';
import Icon56GiftOutline from '@vkontakte/icons/dist/56/gift_outline';

import vkQr from '@vkontakte/vk-qr';
import coin from './tree_coin.png';



const api = new VKMiniAppAPI();
const url = 'https://bot-api-vk.000webhostapp.com/api.place_quests/';

class App extends React.Component {
 constructor(props) {
   super(props)
   this.state = {
	  user:null,
	  activeTab:'all',
	  actievedChose:false,
	  hash:"",
	  rating_team:null,
	  rating_all:null,
	  count:0,
	  activeModal:null,
	  rating_all:null,
	  rating_team:null,
	  snackbar:null,
	  scheme: "bright_light",
	  o:null,
	  start:false,
	  intId:null,
	  isHaveGroup:false
   }
   this.updateConfig = this.updateConfig.bind(this);
   this.myRef = React.createRef();
 }

    componentDidMount() {
		api.initApp();
		
		
		api.onUpdateConfig(this.updateConfig)
		Axios.post(url,JSON.stringify({type:"log_in",obj:{url:window.location.href}})).then(data=>{
			console.log(data)
			 this.setState({user:data.data});
            Axios.post(url, JSON.stringify({type:"if_have_group", hash:data.data.hash})).then(data=>{
				console.log(data);
				if(data.data == 'ok') {
					this.setState({isHaveGroup:true})
				}
			})
		   })
       
	}

	start_and_pause = () => {
		console.log(this.state.start);
		
		if(this.state.start == false){
		  let intervalId = setInterval(()=>{
			Axios.post(url, JSON.stringify({type:'tick', hash:this.state.user.hash}))
			.then(data=>{this.setState({user:{
				...this.state.user,
				current_ballans:(data.data).toFixed(3)
			}})})
		  }, 999)
		   this.setState({intId:intervalId, start:!this.state.start});
		} else {
			clearInterval(this.state.intId);
			this.setState({start:!this.state.start});
		}
	}

	 updateConfig (o) {
		console.log(o);
		this.setState({scheme:o.scheme})
	 }


	 get_rating=()=> {
		 this.setState({activeModal:'rating'})
		Axios.post(url, JSON.stringify({type:"get_rating"})).then(data=>{
            console.log(data);
            this.setState({rating_all: data.data})
            })
          Axios.post(url, JSON.stringify({type:"get_rating_team"}))
          .then(data=>{
            this.setState({rating_team: data.data})
          })  
	}



	


	setSnackbar = (data) => {
		this.setState({snackbar: data});
	}	

	openQRreader = () => {
		api.openCodeReader()
		.then(data=>{
			Axios.post(url, JSON.stringify({type:"set_k", hash:this.state.user.hash, key:data}))
			.then(data=>{
               if(data.data == 'not') {
				   this.setSnackbar(<Snackbar before={<Icon28CancelCircleOutline fill="#FB2626"/>} onClose={()=>this.setState({snackbar: null})}>Ошибочка</Snackbar>)
			   }else {
				   this.setSnackbar(<Snackbar before={<Icon28DoneOutline fill="#00CE40"/>} onClose={()=>this.setState({snackbar: null})}>Ok</Snackbar>)
				this.setState({user:{
					...this.state.user,
					k:(Number(this.state.user.k)+Number(data.data)).toFixed(3)
				}})
			   }
			})
		})
	}	

	join_by_hash = (hash) => {
		Axios.post(url, JSON.stringify({type:"join_group", hash:this.state.user.hash, key:hash}))
		.then(data=>{
			if(data.data == 'not') {
			  this.setSnackbar(<Snackbar before={<Icon28CancelCircleOutline fill="#FB2626"/>} onClose={()=>this.setState({snackbar: null})}>Ошибочка</Snackbar>)
			}else {
			  this.setSnackbar(<Snackbar before={<Icon28DoneOutline fill="#00CE40"/>} onClose={()=>this.setState({snackbar: null})}>Ok</Snackbar>)
			}
		})
	}

	generateQR = () => {
		api.getUserInfo().then(data=>{
			const isShowVkLogo = true;
			const qrSvg = vkQr.createQR(this.state.user.id, isShowVkLogo);
			api.setLocationHash("qr")
			this.setState({qr:qrSvg})
		})

	}


	  joinQR = () => {
		  api.openCodeReader()
		  .then(data=>{
			  Axios.post(url, JSON.stringify({type:"join_group", hash:this.state.user.hash, key:data}))
			  .then(data=>{
				  if(data.data == 'not') {
					this.setSnackbar(<Snackbar before={<Icon28CancelCircleOutline fill="#FB2626"/>} onClose={()=>this.setState({snackbar: null})}>Ошибочка</Snackbar>)
				  }else {
					this.setSnackbar(<Snackbar before={<Icon28DoneOutline fill="#00CE40"/>} onClose={()=>this.setState({snackbar: null})}>Ok</Snackbar>)
				  }
			  })
		  })
	  }


	  onChange =(e)=> {
        const { name, value } = e.currentTarget;
        this.setState({ [name]: value });
	  }
	  


	  pay =()=>{
        api.setLocationHash('');
         api.openCodeReader()
         .then(data=>{
           alert(data);
           
         })
      }



	

	render() {
		return(
	<ConfigProvider isWebView={true} scheme={this.state.scheme}>		
		<View activePanel="home" modal={
                          <ModalRoot activeModal={this.state.activeModal}>
						  <ModalPage id="rating" onClose={()=>this.setState({activeModal: null})} header={<ModalPageHeader>Рейтинг</ModalPageHeader>} dynamicContentHeight>
		  
							  <Tabs>
								<TabsItem selected={this.state.activeTab == 'all'} onClick={()=>this.setState({activeTab: 'all'})}>Личный</TabsItem>
								<TabsItem selected={this.state.activeTab == 'friends'} onClick={()=>this.setState({activeTab: 'friends'})}>Командный</TabsItem>
							  </Tabs>
		  
		  
							  {this.state.activeTab === 'all' ? 
							   <div>
								 {this.state.rating_all != null ? 
							   <List>
								 {this.state.rating_all.map((data,i)=>
								   <Cell key={i} before={<Avatar src={data.photo_200} />} indicator={"+"+data.k+" /c"} asideContent={data.current_ballans}>{data.name}</Cell>
								 )}
								  
							   </List>:
							   <Spinner size="large" style={{marginTop:5}}/> }
							   </div>  
							   :
							   <List>
								   {this.state.rating_team.map((data,i)=>
								   <Cell key={i} before={<Avatar src={data.img} />} multiline  asideContent={data.sum} indicator={"+"+data.k+" /c"}>{data.name}</Cell>
								 )}
							   </List>
				}
						  </ModalPage>
		  
						  <ModalPage id="join" header={<ModalPageHeader>Присоединиться</ModalPageHeader>} onClose={()=>this.setState({activeModal: null})} dynamicContentHeight>
							<CardGrid style={{marginTop:0, paddindBottom:100}}>
							  <Card mode="shadow" size="m" >
								<div style={{height:90}} align="center" onClick={()=>{this.state.joinQR(); this.setState({actievedChose:false, activeModal: null})}}>
								   <Icon56FaceIdOutline style={{marginTop:12}}/>
								   <span>Отсканировать QR</span>
								</div>
							  </Card>
							 
							  <Card mode="shadow" size="m" >
								<div style={{height:90}} align="center" onClick={()=>this.setState({actievedChose:true})}>
								  {this.state.actievedChose ? <Icon16Fire style={{position:"absolute", right:"7%"}} fill="#FF681C"/>:<div />}
								 
								  <Icon56WriteOutline style={{marginTop:12}}/>
								  <span>По хэшу</span>
								</div>
							  </Card>
							</CardGrid>
						   {this.state.actievedChose ? <FormLayout>
							 <Input placeholder="Введите хэш" style={{marginTop:12, marginBottom:1}} name="hash" onChange={this.onChange}/>
						   <Button size="xl" onClick={()=>{this.props.join_by_hash(this.state.hash); this.props.back()}}>OK</Button></FormLayout>:<div />}
						  </ModalPage>
		  
						  <ModalPage id="qr" onClose={()=>this.setState({activeModal: null})} header={<ModalPageHeader>Принять по QR</ModalPageHeader>} dynamicContentHeight>
							<div align="center">
						   <object ref={this.myRef} style={{height:300, weight:'100vw'}}></object>
						   </div>
						  </ModalPage>
		  
						  <ModalPage id="transfer" onClose={()=>this.setState({activeModal: null})} header={<ModalPageHeader>Введите сумму клада</ModalPageHeader>}>
						   <FormLayout>
							 <Input name="count" onChange={this.onChange}/>
							 <Button size="xl" onClick={this.pay}>Продолжить</Button>
						   </FormLayout>
						  </ModalPage>
						</ModalRoot>

		}>
          <Panel id="home">
           <PanelHeader left={<PanelHeaderButton onClick={()=>{this.start_and_pause()}}>{this.state.start ? <Icon28Pause  /> : <Icon28Play />}</PanelHeaderButton>}>Home</PanelHeader>
            {this.state.user == null ? <Spinner size="large"/> :
			  <Group align="center" header={<Header mode='secondary' style={{textAlign: 'center'}} aside={"+"+this.state.user.k}>Ваш счет</Header>}>
				  <div>
			     <span id="balls" style={{fontSize:32, fontWeight:"bold"}}>{this.state.user.current_ballans} <Avatar src={coin} size={42} style={{position:"absolute", right:'5%', marginTop:-42}}/></span>
				  </div>
				 <CardGrid style={{paddingTop:20, paddingBottom:15}}>
				   <Card size='l' style={{backgroundImage: 'linear-gradient(135deg, #f24973 0%, #3948e6 100%)'}} onClick={this.openQRreader}>
					   <div style={{height:85}} align='center'>
						   <Icon48Camera fill="#FFFFFF" style={{marginTop:12}}/>  
						   <span color='white' style={{color: 'white'}}>Сканировать</span>
					   </div>
				   </Card>
				   <Card size='l'>
				        <div style={{height:85}} align='center' onClick={()=>this.get_rating()}>
							<Icon56LinkCircleOutline style={{marginTop:12}}/>
							<span>Рейтинг</span>
						</div>
				   </Card>
				   {this.state.isHaveGroup == false ? 
				   	 <Card size='l' style={{backgroundImage: 'linear-gradient(135deg, #9AFF7F 0%, #0BE1F5 100%)'}}>
						  <div style={{height:85}} align='center' onClick={()=>this.setState({activeModal:'join'})}>
						  <Icon56UserAddOutline style={{marginTop:12}} />
						  <span>Присоединиться к группе</span>
					   </div>
					 </Card>:
					 <Card size='l' style={{backgroundColor:"#FA5959"}}>
						 <div style={{height:85}} align='center' onClick={()=>{Axios.post(url, JSON.stringify({type:'delete',hash:this.state.user.hash}));this.setState({isHaveGroup:false})}}>
                           <Icon56DeleteOutlineAndroid style={{marginTop:12}}/>
						   <span>Выйти из группы</span>
						 </div>
					 </Card>
				}

					<Card size='l' style={{ backgroundColor:"#448BF3"}}>
						<div style={{height:85}} align='center' onClick={()=>this.setState({activeModal:'transfer'})}>
							<Icon56GiftOutline fill="#fff" style={{marginTop:12}}/>
							<span style={{color:"white"}}>Спрятать клад</span>
						</div>
					</Card>
				 </CardGrid>
			  </Group>
			}
			{this.state.snackbar}
		  </Panel>
		</View>
	</ConfigProvider>	
		)
	}

}
export default App;

